import {Button, DatePicker, Form, Icon, Input, InputNumber, message, Radio, Tooltip} from "antd";
import md5 from "md5";
import axios from "axios";
import ApiUtil from "../../utils/ApiUtil";
import HttpUtil from "../../utils/HttpUtil";
import React from "react";

const key = 'updatable';

@Form.create()
class RegisterForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            validateStatus: null,

            confirmDirty: false,
            agree: false
        };
    }

    // Form


    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {

            if (!err) {
                let register = values
                register.birth = register.birth.format('YYYY-MM-DD')
                register.password = md5(register.password)
                message.loading({content: '...', key});
                axios(
                    {
                        method: "post",
                        url: ApiUtil.URL_IP + ApiUtil.API_REGISTER_USER,
                        data: register
                    }
                ).then(
                    (res) => {
                        if (res.data.code === 0) {
                            message.success({content: res.data.msg, key, duration: 2});
                            this.props.form.resetFields();
                        } else {
                            message.error({content: res.data.msg, key, duration: 2});
                        }


                    }
                ).catch((res) => {
                    message.error(res.data.msg)
                })
            }
        });
    };

    handleConfirmBlur = e => {
        const {value} = e.target;
        this.setState({confirmDirty: this.state.confirmDirty || !!value});
    };
    validateUsername = (rule, value, callback) => {
        if (value) {
            HttpUtil.get(ApiUtil.API_CHECK_USER + value)
                .then(
                    re => {
                        if (re.code === 1) {
                            callback('')
                        } else {
                            callback();
                        }

                    }
                ).catch(error => {
                message.error(error.message);
            });
        }

    }
    compareToFirstPassword = (rule, value, callback) => {
        const {form} = this.props;
        if (value && value !== form.getFieldValue('password')) {
            callback('!');
        } else {
            callback();
        }
    };

    validateToNextPassword = (rule, value, callback) => {
        const {form} = this.props;
        if (value && this.state.confirmDirty) {
            form.validateFields(['confirm'], {force: true});
        }
        callback();
    };


    render() {


        //form const settings
        const {getFieldDecorator} = this.props.form;

        const formItemLayout = {
            labelCol: {
                xs: {span: 24},
                sm: {span: 8},
            },
            wrapperCol: {
                xs: {span: 24},
                sm: {span: 16},
            },
        };
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0,
                },
                sm: {
                    span: 16,
                    offset: 8,
                },
            },
        };

        return (
            <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                <Form.Item
                    hasFeedback
                    label={
                        <span>
                                                &nbsp;
                            <Tooltip title="">
                                                <Icon type="question-circle-o"/>
                                                </Tooltip>
                                            </span>
                    }
                    extra={<span>
                                                 <Icon type="info-circle-o"/>&nbsp;
                                            </span>}
                >
                    {getFieldDecorator('username', {
                        rules: [{
                            required: true,
                            message: '!',
                            whitespace: true
                        }, {validator: this.validateUsername}
                        ],
                    })(<Input/>)}

                </Form.Item>

                <Form.Item label="" hasFeedback>
                    {getFieldDecorator('password', {
                        rules: [
                            {
                                required: true,
                                message: '!',
                            },
                            {
                                validator: this.validateToNextPassword,
                            },
                        ],
                    })(<Input.Password/>)}
                </Form.Item>
                <Form.Item label="" hasFeedback>
                    {getFieldDecorator('confirm', {
                        rules: [
                            {
                                required: true,
                                message: '!',
                            },
                            {
                                validator: this.compareToFirstPassword,
                            },
                        ],
                    })(<Input.Password onBlur={this.handleConfirmBlur}/>)}
                </Form.Item>
                <Form.Item
                    label={
                        <span>
                                                &nbsp;
                            <Tooltip title="?">
                                                <Icon type="question-circle-o"/>
                                                </Tooltip>
                                            </span>
                    }
                >
                    {getFieldDecorator('nickname', {
                        rules: [{
                            required: true,
                            message: '!',
                            whitespace: true
                        }],
                    })(<Input/>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('sex', {
                        rules: [{
                            required: true,
                            message: ""
                        }]
                    })(
                        <Radio.Group>
                            <Radio value={''}></Radio>
                            <Radio value={''}></Radio>
                        </Radio.Group>,
                    )}
                </Form.Item>
                <Form.Item label=''>
                    {getFieldDecorator('age', {
                        rules: [{required: true, message: ""}]
                    })(<InputNumber min={1} max={120}/>)}
                    <span className="ant-form-text"> </span>
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('birth', {
                        rules: [{type: 'object', required: true, message: '!'}],
                    })(<DatePicker placeholder=''/>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('email', {
                        rules: [
                            {
                                type: 'email',
                                message: '!',
                            },
                            {
                                required: true,
                                message: '!',
                            },
                        ],
                    })(<Input/>)}
                </Form.Item>

                <Form.Item label="">
                    {getFieldDecorator('phone', {
                        rules: [{required: true, message: '!'}],
                    })(<Input addonBefore={'+86'} style={{width: '100%'}}/>)}
                </Form.Item>

                <Form.Item {...tailFormItemLayout}>
                    <Button type="primary" htmlType="submit" >
                        
                    </Button>
                </Form.Item>
            </Form>
        )

    }


}

export default RegisterForm


